<template>
    <div id="my-widget">
      <vue-particles            class="touch"
                                style="position: fixed;
                                       width: 100%;
                                       height: 100%;
                                       background: #F5F5F5;
                                       z-index:0;"
                                color="#000000"
                                :particleOpacity="0.7"
                                linesColor="#000000"
                                :particlesNumber="80"
                                shapeType="circle"
                                :particleSize="4"
                                :linesWidth="1"
                                :lineLinked="true"
                                :lineOpacity="0.4"
                                :linesDistance="150"
                                :moveSpeed="3"
                                :hoverEffect="true"
                                hoverMode="grab"
                                :clickEffect="true"
                                clickMode="push"
      />
      <BackTop class="touch"></BackTop>
      <MyBGM></MyBGM>
      <MyLink></MyLink>
    </div>
</template>

<script>
// import MyBGM from './MyBGM'

import MyLink from './MyLink'
export default {
  name: 'MyWidget',
  components: {
    MyLink,
    MyBGM: resolve => {
      require(['./MyBGM'], resolve)
    }
  }
}
</script>

<style scoped>
  #my-widget div{
    z-index: 100;
  }
</style>



